#root{
    background-color: black;
    min-height: 100vh;
    font-family: 'Inconsolata', monospace;
    overflow: hidden;
}

hr{
    border: 0;
    height: .25vw;
    background-image: linear-gradient(to right, rgba(0, 0, 0, 0), rgba(34, 33, 44), rgba(0, 0, 0, 0));
}


body {
    margin: 0;
    color: white;
    cursor: default;  
}

.page-container{
    min-height: 100%;
    margin-left: 5vw;
    margin-right: 5vw;
    padding-bottom: 5vh;
}

a:visited{
    text-decoration: none;
}


